import React, { useRef, useEffect } from 'react';

import './main.scss';

import Content from "./Content/main"
import { IonButton } from '@ionic/react';


const Main: React.FC = () =>
{
	console.log("ShowWindow");

	const cases = ["종류별", "페어링", "테마별"];
	const contents:Array<any> = [];
	let idx_cur = 0;
	
	const ref = useRef(null);
	useEffect(() => {
		let dom: any = ref.current;
		Array.from(dom.children).forEach(val => contents.push(val));
		contents[0].classList.add("selected");
	});

	function setContent(idx: number)
	{
		contents[idx_cur].classList.remove("selected");
		contents[idx].classList.add("selected");
		idx_cur = idx;
	}


	function getBtns()
	{
		return cases.map((case_, idx) =>
		{
			return <IonButton key={idx}
					  		  className="btn"
							  onClick={() => setContent(idx)}>
				{case_}
			</IonButton>;
		});
	}

	function getContents()
	{
		return cases.map((_, idx) => <Content key={idx} db={_} />);
	}
	
	return <div id="main">
		<div id="btns">{getBtns()}</div>
		<div id="contents" ref={ref}>{getContents()}</div>
	</div>
}

const ShowWindow = React.memo(Main, () => true);
export default ShowWindow;
