import React from 'react';
import { Redirect, Route } from 'react-router-dom';
import {
  IonApp,
  IonIcon,
  IonRouterOutlet,
  IonTabBar,
  IonTabButton,
  IonTabs
} from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import { grid, search, camera, cart, person } from 'ionicons/icons';
import MainPage from './pages/MainPage/main';
import Tab2 from './pages/Tab2';
import Tab4 from './pages/Tab4';
import Tab5 from './pages/Tab5';

/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';

/* Theme variables */
import './theme/variables.css';
import './theme/camera.scss'


const App: React.FC = () =>
{
  console.log(App.name);

  function getBase()
  {
    const ele = window.document.getElementsByTagName("base");
    let base = ele[0].getAttribute("href");
    return base ? base : "/";
  }
  
  function getCamera()
  {
    return <label id="camera">
      <input type="file" accept="image/*" capture="environment" />
      <IonIcon icon={camera} />
    </label>
  }

  return (
    <IonApp>
      <IonReactRouter basename={getBase()}>
        <IonTabs>
          <IonRouterOutlet>
            <Route path="/tab2" component={Tab2} exact />
            <Route path="/MainPage" component={MainPage} exact />
            <Route path="/tab4" component={Tab4} exact />
            <Route path="/tab5" component={Tab5} exact />
            <Redirect from="/" to="/MainPage" exact />
          </IonRouterOutlet>
          <IonTabBar slot="bottom">
            <IonTabButton tab="tab2" href="/tab2">
              <IonIcon icon={grid} />
            </IonTabButton>
            <IonTabButton tab="MainPage" href="/MainPage">
              <IonIcon icon={search} />
            </IonTabButton>
            <IonTabButton>
              {getCamera()}
            </IonTabButton>
            <IonTabButton tab="tab4" href="/tab4">
              <IonIcon icon={cart} />
            </IonTabButton>
            <IonTabButton tab="tab5" href="/tab5">
              <IonIcon icon={person} />
            </IonTabButton>
          </IonTabBar>
        </IonTabs>
      </IonReactRouter>
    </IonApp>
  );
}

export default App;
